'use client';

import { useEffect, useState } from 'react';
import { useSession } from 'next-auth/react';
import { useSearchParams } from 'next/navigation';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { 
  ArrowLeft,
  Plus,
  Minus,
  AlertCircle,
  Calendar,
  User,
  RefreshCw,
} from 'lucide-react';
import Link from 'next/link';

interface StockMovementDetail {
  id: string;
  medicationId: string;
  medicationName: string;
  pharmaceuticalForm: string;
  type: 'ENTRY' | 'EXIT' | 'CORRECTION';
  quantity: number;
  reason?: string;
  previousStock: number;
  newStock: number;
  createdAt: string;
  user?: {
    username: string;
  };
}

export default function MovementDetailsPage() {
  const { data: session } = useSession();
  const searchParams = useSearchParams();
  const movementType = searchParams.get('type') || 'ENTRY';
  
  const [movements, setMovements] = useState<StockMovementDetail[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [sortBy, setSortBy] = useState<'date' | 'medication' | 'quantity'>('date');

  const fetchMovements = async () => {
    setIsLoading(true);
    try {
      const response = await fetch(`/api/medications/movements?type=${movementType}`);
      if (response.ok) {
        const data = await response.json();
        setMovements(data.movements || []);
      }
    } catch (error) {
      console.error('Erreur lors du chargement des mouvements:', error);
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    if (session?.user?.role === 'admin') {
      fetchMovements();
    }
  }, [session, movementType]);

  const getMovementInfo = () => {
    switch (movementType) {
      case 'ENTRY':
        return {
          title: 'Quantit√© Ajouter',
          description: 'Tous les ajouts en stock (venant de la page Mouvement de stock)',
          icon: Plus,
          color: 'bg-green-100 text-green-800',
          textColor: 'text-green-700',
        };
      case 'EXIT':
        return {
          title: 'Qtt√© Sortie',
          description: 'Toutes les sorties/ventes (venant de la page Vente)',
          icon: Minus,
          color: 'bg-orange-100 text-orange-800',
          textColor: 'text-orange-700',
        };
      case 'CORRECTION':
        return {
          title: 'Qtt√© Abim√©e',
          description: 'Toutes les corrections (venant de la page Stock)',
          icon: AlertCircle,
          color: 'bg-red-100 text-red-800',
          textColor: 'text-red-700',
        };
      default:
        return {
          title: 'Mouvements',
          description: 'D√©tail des mouvements',
          icon: Calendar,
          color: 'bg-gray-100 text-gray-800',
          textColor: 'text-gray-700',
        };
    }
  };

  const movementInfo = getMovementInfo();
  const MovementIcon = movementInfo.icon;

  const sortedMovements = [...movements].sort((a, b) => {
    switch (sortBy) {
      case 'date':
        return new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime();
      case 'medication':
        return a.medicationName.localeCompare(b.medicationName);
      case 'quantity':
        return Math.abs(b.quantity) - Math.abs(a.quantity);
      default:
        return 0;
    }
  });

  const totalQuantity = movements.reduce((sum, m) => sum + Math.abs(m.quantity), 0);

  if (session?.user?.role !== 'admin') {
    return (
      <div className="p-6">
        <div className="text-center">
          <h1 className="text-2xl font-bold text-red-600">Acc√®s refus√©</h1>
          <p className="text-gray-600 mt-2">Vous n'avez pas les permissions pour acc√©der √† cette page.</p>
        </div>
      </div>
    );
  }

  return (
    <div className="p-6 space-y-6">
      {/* En-t√™te avec retour */}
      <div className="flex items-center gap-4 mb-6">
        <Link href="/admin/inventory">
          <Button variant="outline" size="sm" className="cursor-pointer">
            <ArrowLeft className="h-4 w-4 mr-2" />
            Retour √† l'inventaire
          </Button>
        </Link>
      </div>

      {/* Titre et description */}
      <div>
        <div className="flex items-center gap-3 mb-2">
          <MovementIcon className={`h-8 w-8 ${movementInfo.textColor}`} />
          <h1 className="text-3xl font-bold text-gray-900">{movementInfo.title}</h1>
        </div>
        <p className="text-gray-600">{movementInfo.description}</p>
      </div>

      {/* Statistiques */}
      <Card>
        <CardContent className="p-6">
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            <div>
              <p className="text-sm text-gray-600 mb-2">Nombre de mouvements</p>
              <p className="text-3xl font-bold text-gray-900">{movements.length}</p>
            </div>
            <div>
              <p className="text-sm text-gray-600 mb-2">Quantit√© totale</p>
              <p className={`text-3xl font-bold ${movementInfo.textColor}`}>
                {totalQuantity} unit√©s
              </p>
            </div>
            <div>
              <p className="text-sm text-gray-600 mb-2">Moyenne par mouvement</p>
              <p className="text-3xl font-bold text-gray-900">
                {movements.length > 0 ? Math.round(totalQuantity / movements.length) : 0} unit√©s
              </p>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Contr√¥les de tri et actualisation */}
      <Card>
        <CardContent className="p-4">
          <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4">
            <div className="flex gap-2">
              <Button
                variant={sortBy === 'date' ? 'default' : 'outline'}
                onClick={() => setSortBy('date')}
                size="sm"
                className="cursor-pointer"
              >
                Trier par date
              </Button>
              <Button
                variant={sortBy === 'medication' ? 'default' : 'outline'}
                onClick={() => setSortBy('medication')}
                size="sm"
                className="cursor-pointer"
              >
                Trier par m√©dicament
              </Button>
              <Button
                variant={sortBy === 'quantity' ? 'default' : 'outline'}
                onClick={() => setSortBy('quantity')}
                size="sm"
                className="cursor-pointer"
              >
                Trier par quantit√©
              </Button>
            </div>
            <Button
              onClick={fetchMovements}
              disabled={isLoading}
              variant="outline"
              size="sm"
              className="cursor-pointer"
            >
              <RefreshCw className={`h-4 w-4 ${isLoading ? 'animate-spin' : ''}`} />
            </Button>
          </div>
        </CardContent>
      </Card>

      {/* Tableau des mouvements */}
      <Card>
        <CardHeader>
          <CardTitle>D√©tail des mouvements ({sortedMovements.length})</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="overflow-x-auto">
            <table className="w-full text-sm">
              <thead>
                <tr className="border-b bg-gray-50">
                  <th className="text-left p-3 font-semibold">üìÖ Date</th>
                  <th className="text-left p-3 font-semibold">üíä Produit</th>
                  <th className="text-left p-3 font-semibold">ÔøΩ Forme</th>
                  <th className="text-right p-3 font-semibold">üì¶ Quantit√©</th>
                  <th className="text-left p-3 font-semibold">Utilisateur</th>
                  <th className="text-left p-3 font-semibold">Raison</th>
                </tr>
              </thead>
              <tbody>
                {sortedMovements.map((movement) => (
                  <tr key={movement.id} className="border-b hover:bg-gray-50 transition">
                    <td className="p-3 whitespace-nowrap">
                      <div className="font-semibold text-gray-900">
                        {new Date(movement.createdAt).toLocaleDateString('fr-FR')}
                      </div>
                      <div className="text-xs text-gray-500">
                        {new Date(movement.createdAt).toLocaleTimeString('fr-FR')}
                      </div>
                    </td>
                    <td className="p-3">
                      <p className="font-bold text-gray-900">{movement.medicationName}</p>
                    </td>
                    <td className="p-3 text-gray-600">
                      <p className="font-medium">{movement.pharmaceuticalForm}</p>
                    </td>
                    <td className="p-3 text-right">
                      <Badge className={movementInfo.color}>
                        <MovementIcon className="h-3 w-3 mr-1" />
                        {Math.abs(movement.quantity)}
                      </Badge>
                    </td>
                    <td className="p-3 text-gray-600">
                      {movement.user?.username || 'N/A'}
                    </td>
                    <td className="p-3 text-gray-600 text-xs">
                      {movement.reason || '-'}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>

            {sortedMovements.length === 0 && (
              <div className="text-center py-8 text-gray-500">
                <AlertCircle className="h-12 w-12 mx-auto mb-4 opacity-50" />
                <p>Aucun mouvement trouv√©.</p>
              </div>
            )}
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
